.header_container {
  background-color: #fff;
  border: 1px solid #ded5d5;
  border-radius: 8px;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  transition: box-shadow .3s;
  display: flex;
  box-shadow: inset 0 0 7px #02020240;
}

.app-container {
  text-size-adjust: auto;
  min-width: 1240px;
  max-width: 100%;
  height: 100%;
}

.app-logo {
  align-items: start;
  display: flex;
}

.navitems_container {
  display: flex;
}

.navitems_list {
  justify-content: space-evenly;
  gap: 5px;
  width: 100%;
  list-style: none;
  display: flex;
}

.navitemslink {
  color: #000;
  text-decoration: none;
}

.navitemslink:hover {
  color: red;
  cursor: pointer;
  transform: scale(1.1);
}

.navitems {
  cursor: pointer;
  justify-content: center;
  align-items: center;
  width: max-content;
  height: 3rem;
  margin: .5rem;
  font-weight: 700;
  display: flex;
}

.navitems:hover {
  color: red;
  cursor: pointer;
  transform: scale(1.1);
}

.image-app-logo {
  border-radius: 50%;
  min-width: 25px;
  max-width: 50px;
  min-height: 25px;
  max-height: 50px;
}

.shimmer-container {
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 15px;
  width: 100%;
  margin: 10px 0;
  padding: 10px 0;
  display: grid;
}

.shimmer-res-logo {
  background-color: #fff;
  border-radius: 8px;
  width: 200px;
  height: 220px;
}

.phantom-card {
  background: linear-gradient(-45deg, #eee 40%, #fafafa 50%, #eee 60%) 100% 0 / 300%;
  border: 1px solid #ddd;
  border-radius: 8px;
  align-items: center;
  height: 300px;
  padding: 5px;
  transition: box-shadow .3s;
  animation: 1s linear infinite shimmer;
  display: block;
}

@keyframes shimmer {
  to {
    background-position-x: 0%;
  }
}

.connectionmodalcontainer {
  color: #fff;
  z-index: 9999;
  pointer-events: auto;
  background: #02060ce6;
  border: 1px solid #000;
  border-radius: 8px;
  align-items: center;
  gap: 30px;
  width: auto;
  padding: 15px;
  display: flex;
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateY(-50%)translate(-50%);
}

.refreshbtton {
  color: #fff;
  border: unset;
  background: unset;
}

.refreshbtton:hover {
  font-weight: 700;
  transform: scale(1.25);
}

.online_status {
  display: flex;
  position: relative;
}

.online_status_tooltip_text {
  visibility: hidden;
  z-index: 1;
  color: gray;
  opacity: .8;
  width: max-content;
  position: absolute;
  top: 25px;
}

.online_status:hover .online_status_tooltip_text {
  visibility: visible;
}

