.bodycontainer {
  background: #fff;
  padding: 10px;
  overflow-x: hidden;
}

.searchcontainer {
  margin-top: 2rem;
}

.cardcontainer {
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 15px;
  width: 100%;
  margin: 10px 0;
  padding: 10px 0;
  display: grid;
}

.restaurantcard {
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  align-items: center;
  width: 220px;
  height: 360px;
  padding: 5px;
  transition: box-shadow .3s;
  display: block;
}

.restaurantcard:hover {
  cursor: pointer;
  transform-origin: center;
  border: 1px solid #aaa;
  transition: all .1s;
  transform: scale(.9);
  box-shadow: 3px 4px 12px #00000040;
}

.res-logo-container {
  justify-content: center;
  align-items: center;
  width: 100%;
  display: flex;
}

.res-logo {
  border-radius: 8px;
  width: 190px;
  height: 200px;
}

.restaurantname {
  margin-top: 10px;
  font-family: Gilroy;
  font-weight: bolder;
}

.restaurantaddress {
  margin-top: 10px;
  position: relative;
  bottom: 0;
}

.restaurantrating {
  grid-auto-flow: column;
  justify-content: start;
  align-items: center;
  gap: 2px;
  margin-top: 10px;
  display: grid;
}

.restaurantratingvalue-deliverytime {
  letter-spacing: -.4px;
  color: #02060ceb;
  overflow-wrap: break-word;
  -webkit-box-orient: vertical;
  width: 100%;
  font-family: Gilroy;
  font-size: 16px;
  font-weight: bolder;
  line-height: 21px;
  display: -webkit-box;
  overflow: hidden;
}

.mg-top {
  margin-top: 10px;
}

.mg-left {
  margin-left: 10px;
}

.flexbox {
  display: flex;
}

.contact-container {
  background: #fefefe;
  border-radius: 8px;
  max-width: 600px;
  margin: 50px auto;
  padding: 2rem;
  box-shadow: 0 0 10px #0000001a;
}

.contact-form {
  flex-direction: column;
  gap: 1rem;
  display: flex;
}

.contact-form label {
  flex-direction: column;
  font-weight: bold;
  display: flex;
